import React, { Component } from 'react'

export default class App extends Component {
constructor(props){
  super(props);
  this.state = {}
}

    componentDidMount(){
        const url = "https://jogavidia.github.io/ctperu-json/ctperu.json";

        fetch(url)
            .then(result => result.json())
            .then(result => {
              this.setState({
                ctData: result
              })
            });
    }

  render() {
    if (!this.state.ctData) return <p>Loading...</p>
    return(
      <div className="wrap">
        <h1>{this.state.ctData.blog}</h1>
        <p>{this.state.ctData.about}</p>
        <div className="container">
        {
          this.state.ctData.cities.map((postDetail, index) =>{
            return (
              <div key={index} className="info">
                <h2>{postDetail.cityName}</h2>
                <p> <span>{postDetail.place}</span>{postDetail.description}</p>
                <div className="embajador">
                  <p>{postDetail.gender}<a href={"https://www.instagram.com/" + postDetail.embajador}>@{postDetail.embajador}</a></p>
                </div>
              </div>
            )
          })
        }
        </div>
      </div>
    )
  }
}
